import React, { Component } from "react";

import { Navbar, Nav, Dropdown } from "react-bootstrap";
import { withTranslation } from 'react-i18next';
import NotificationBar from "../SidebarDisappearing/NotificationBar";
import EmailBar from "../SidebarDisappearing/EmailBar";

import './NavbarComponent.css';
import './NavbarComponent.scss';
import ico from '../../Assets/bg1.jpg';
import { RiHomeSmileFill, RiUser5Fill, RiSettings4Fill, RiLogoutCircleLine } from 'react-icons/ri';




export default withTranslation()(class NavbarComponent extends Component {

    render() {

        const { t } = this.props;


        return (
            <>

                <Navbar id="navbarTop" sticky="top">
                    <Navbar.Brand href="#home">myForment</Navbar.Brand>
                    <Nav className="ml-auto">

                        <EmailBar />

                        <NotificationBar />

                        <Dropdown>
                            <Dropdown.Toggle id="dropdown-basic" style={{ 'backgroundColor': 'none' }}>
                                <span style={{ 'verticalAlign': 'super' }}>{t("navbar:Hi")} <strong>dellantonio47@gmail.com</strong></span>
                                <div className="MuiAvatar-root MuiAvatar-circle MuiAvatar-colorDefault">
                                    <img src={ico} className="MuiIcon-root MuiAvatar-fallback" />
                                </div>
                            </Dropdown.Toggle>

                            <Dropdown.Menu>
                                <Dropdown.Item href="#/action-1"><RiHomeSmileFill /><span className="pl-2">{t("navbar:Home")}</span></Dropdown.Item>
                                <Dropdown.Item href="#/action-2"><RiUser5Fill /><span className="pl-2">{t("navbar:Profile")}</span></Dropdown.Item>
                                <Dropdown.Item href="#/action-3"><RiSettings4Fill /><span className="pl-2">{t("navbar:Settings")}</span></Dropdown.Item>
                                <Dropdown.Item href="#/action-3"><RiLogoutCircleLine /><span className="pl-2">{t("navbar:Logout")}</span></Dropdown.Item>
                            </Dropdown.Menu>
                        </Dropdown>
                    </Nav>
                </Navbar>

            </>
        );
    }

});