import i18n from "i18next";
import LanguageDetector from "i18next-browser-languagedetector";
import XHR from "i18next-xhr-backend";

import translationEng from "./en/translation.json";
import translationIta from "./it/translation.json";

import navbarEng from "./en/navbar.json";
import navbarIta from "./it/navbar.json";

import sidebarEng from "./en/sidebar.json";
import sidebarIta from "./it/sidebar.json";

i18n
    .use(XHR)
    .use(LanguageDetector)
    .init({
        debug: true,
        lng: "en",
        fallbackLng: "en", // use en if detected lng is not available

        keySeparator: false, // we do not use keys in form messages.welcome

        interpolation: {
            escapeValue: false // react already safes from xss
        },

        resources: {

            en: {
                translations: translationEng,
                navbar: navbarEng,
                sidebar: sidebarEng

            },
            it: {
                translations: translationIta,
                navbar: navbarIta,
                sidebar: sidebarIta
            }
        },
        // have a common namespace used around the full app
        ns: ["translations", "navbar", "sidebar"],
        defaultNS: "translations"
    });

export default i18n;