import React, { Component } from 'react';
import { ThemeProvider } from 'styled-components';
import { lightTheme, darkTheme } from './Theme/theme';
import { GlobalStyles } from './Theme/global';
import MyformentApp from "./Structure/MyformentApp";
import './App.scss';
import 'bootstrap/dist/css/bootstrap.min.css';

export default class App extends Component {

  state = {
    theme: window.localStorage.getItem('theme') || 'light'
  }

  handleToggleTheme = () => {
    if (this.state.theme === 'light') {
        window.localStorage.setItem('theme', 'dark');
        this.setState({theme: 'dark'});
    } else {
        window.localStorage.setItem('theme', 'light');
        this.setState({theme: 'light'});
    }
}

  render() {

    let themeOBJ = this.state.theme === 'light' ? lightTheme : darkTheme;
    let theme = this.state.theme === 'light' ? 'light' : 'dark';

    return (

      <ThemeProvider theme={themeOBJ}>
        <>
          <GlobalStyles />
          <MyformentApp toggleTheme={this.handleToggleTheme} theme={theme} ></MyformentApp>
        </>
      </ThemeProvider>
    );

  }

}

