import React, { Component } from "react";
import Switch from 'react-switch';
import { Link } from 'react-router-dom';
import SidebarItems from "./SidebarItems";
import LanguagesItems from "../../Locales/LanguagesItems";
import { ProSidebar, Menu, MenuItem, SubMenu, SidebarHeader, SidebarFooter, SidebarContent, } from 'react-pro-sidebar';
import "./SidebarComponent.css"
import { FaTachometerAlt, FaGithub } from 'react-icons/fa';
import sidebarBg from '../../Assets/bg1.jpg'
import { withTranslation } from "react-i18next";
import i18n from "../../Locales/i18n";

import Toggle from "../../Theme/Toggle.js";





export default withTranslation()(class SidebarComponent extends Component {

    constructor(props) {
        super(props);

        console.log("PROPS: %O", props);

        /* Setting translation looking the localstorage (the localstorage accepts only String!!) */
        this.state = {
            translation: window.localStorage.getItem('translation') || "en",


            collapsed: window.localStorage.getItem('SidebarCollapsed') === "true" ? true : false,
            image: window.localStorage.getItem('SidebarImage') === "false" ? false : true

        };

        i18n.changeLanguage(this.state.translation);
    }

    /* Handle of translation and image change */
    handleTranslationChange = lang => {
        this.setState(prevState => ({ translation: lang }));
        i18n.changeLanguage(lang);
        window.localStorage.setItem('translation', lang);
    };

    handleCollapsedChange = (checked) => {
        this.setState({ collapsed: checked });
        window.localStorage.setItem('SidebarCollapsed', "" + checked);
    };

    handleImageChange = (checked) => {
        this.setState({ image: checked });
        window.localStorage.setItem('SidebarImage', "" + checked);
    };

    render() {

        const { t } = this.props;

        return (
            <ProSidebar
                image={this.state.image ? sidebarBg : false}
                collapsed={this.state.collapsed}
                toggled={this.props.toggled}
                breakPoint="md"
                onToggle={this.props.handleToggleSidebar}
            >
                <SidebarHeader>
                    <div
                        style={{
                            padding: '24px',
                            textTransform: 'uppercase',
                            fontWeight: 'bold',
                            fontSize: 14,
                            letterSpacing: '1px',
                            overflow: 'hidden',
                            textOverflow: 'ellipsis',
                            whiteSpace: 'nowrap',
                        }}
                    >
                        Pro Sidebar
                    </div>
                </SidebarHeader>

                <SidebarContent>
                    {
                        SidebarItems.map((item, index) => {
                            return (
                                <Menu key={index} iconShape="circle">
                                    <Link to={item.route}>
                                        <MenuItem
                                            key={item.name} active={''/*index === activeIndex*/}
                                            icon={<FaTachometerAlt />}
                                            suffix={<span className="badge red">NEW</span>}
                                        >
                                            {item.name}
                                        </MenuItem>
                                    </Link>
                                </Menu>
                            );
                        })
                    }
                </SidebarContent>

                <SidebarFooter style={{ textAlign: 'center' }}>
                    <div className={`block switch${this.state.collapsed ? '-collapsed' : ''}`}>
                        <Switch
                            height={16}
                            width={30}
                            checkedIcon={false}
                            uncheckedIcon={false}
                            onChange={this.handleCollapsedChange}
                            checked={this.state.collapsed}
                            onColor="#219de9"
                            offColor="#bbbbbb"
                        />
                        <span> {t("sidebar:Collapse")} </span>
                    </div>
                    <div className={`block switch${this.state.collapsed ? '-collapsed' : ''}`}>
                        <Switch
                            height={16}
                            width={30}
                            checkedIcon={false}
                            uncheckedIcon={false}
                            onChange={this.handleImageChange}
                            checked={this.state.image}
                            onColor="#219de9"
                            offColor="#bbbbbb"
                        />
                        <span> {t("sidebar:Image")} </span>
                    </div>

                    <div className="sidebar-btn-wrapper">

                        {
                            LanguagesItems.map((item, index) => {
                                return (
                                    <a key={index} onClick={() => this.handleTranslationChange(item.cod)} className="sidebar-btn">
                                        {item.name}
                                    </a>
                                );
                            })
                        }

                    </div>

                    <div className="sidebar-btn-wrapper">
                        <Toggle theme={"" + this.props.theme} toggleTheme={this.props.toggleTheme} />
                    </div>

                    <div className="sidebar-btn-wrapper" style={{ padding: '20px 24px' }}>

                        <a
                            href="https://github.com/azouaoui-med/react-pro-sidebar"
                            target="_blank"
                            className="sidebar-btn"
                            rel="noopener noreferrer"
                        >
                            <FaGithub />
                            <span> viewSource </span>
                        </a>
                    </div>
                </SidebarFooter>
            </ProSidebar>
        );
    }

});